#modal-map {
  width: 0;
  height: 0;
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  border: 10px solid #fff;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

#modal-map.modal-show {
  width: calc(100vw - 40px);
  height: calc(100vh - 40px);
  opacity: 1;
  visibility: visible;
  background-color: #9abfdd;
  border: 10px solid #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#modal-map .modal__content {
  width: 100%;
  height: 100%;
  position: relative;
}

#modal-map .modal__close {
  z-index: 9;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  position: absolute;
  top: 10px;
  right: 5px;
}

#modal-map .modal__close svg {
  fill: #05001480;
  transition: all .2s ease-in-out;
}

#modal-map .modal__close:hover svg {
  fill: #050014bf;
  transform: scale(1.2);
}

#modal-map .modal__image-container {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
}

#modal-map .modal__image {
  width: 2000px;
  height: 3200px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.modal__image-container.pan-up {
  top: calc(50% + 50px);
}

.modal__image-container.pan-down {
  top: calc(50% - 50px);
}

.modal__image-container.pan-left {
  left: calc(50% + 50px);
}

.modal__image-container.pan-right {
  left: calc(50% - 50px);
}

.modal__image--pan {
  z-index: 9;
  text-align: center;
  flex-direction: column;
  align-items: center;
  animation: 10s infinite shake;
  display: flex;
  position: absolute;
  bottom: -10px;
  left: -10px;
  scale: .75;
}

.modal__image--pan-span {
  z-index: 10;
  color: #050014e6;
  padding-top: 10px;
  display: inline-block;
  position: absolute;
  bottom: 10px;
  left: 0;
}

